.wrapper {
  position: absolute;
  margin: auto;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  width: fit-content;
  text-align: center;
  z-index: 4;

  span {
    &:first-letter {
      letter-spacing: 12vmax;
    }

    position: relative;
    color: #3cb64a;
    font-weight: 900;
    font-size: 20.4em;
    display: block;
    overflow: hidden;
    width: fit-content;
    height: max-content;

    @media (max-width: 700px) {
      & {
        font-size: 17.4em;
      }
    }

    @media (max-width: 470px) {
      & {
        font-size: 15.4em;
      }
    }

    &:before {
      content: '';
      background-image: url('../../assets/error404.png');
      position: absolute;
      height: 100%;
      width: 100%;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      animation: rotateIn 0.5s ease-out;
    }
  }

  p {
    text-align: center;
    font-style: italic;
    font-weight: 400;
    color: #3cb64a;
    margin-top: 0;
    line-height: 22px;
  }

  button {
    background-color: #af3c21;
    border: 0;
    padding: 11px 22px;
    border-radius: 50px;
    color: #fff;
    margin-top: 10px;
    cursor: pointer;
    font-weight: 900;
  }
}


@keyframes spin {

  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(-360deg);
  }
}

@keyframes rotateIn {
  from {
    transform: rotate(0deg) scale(0.2);
    opacity: 0;
  }
  to {
    transform: rotate(360deg) scale(1);
    opacity: 1;
  }
}